.radio-label {
  position: relative;
  display: flex;
  align-items: baseline;
  padding-left: 1.5rem;
  min-height: calc(1rem + 4px);
  min-width: calc(1rem + 2px);
  cursor: pointer;
  font-weight: normal;
}

.faux-checkbox::before, .faux-checkbox::after,
.faux-radio::before,
.faux-radio::after,
.checkbox-label::before,
.checkbox-label::after,
.radio-label::before,
.radio-label::after {
