.faux-checkbox::after,
.faux-radio::after,
.checkbox-label::after,
.radio-label::after {
  width: 0.5rem;
  height: 0.5rem;
  top: calc( 1rem / 2 + 1px);
  left: 0.5rem;
  background: #e35205;
  transform: translate(-50%, -50%) scale(1);
}


.faux-radio::before,
.faux-radio::after,
.radio-label::before,
.radio-label::after {
  border-radius: 1rem;
}

/*
