}

.faux-checkbox[aria-checked="true"]::after,
.faux-radio[aria-checked="true"]::after,
.checkbox-input:checked + .checkbox-label::after,
.radio-input:checked + .radio-label::after {
  opacity: 1;
}

.faux-checkbox:not([aria-checked="true"])::after,
.faux-radio:not([aria-checked="true"])::after,
