/**
 * GENERAL
 */
.card {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  max-width: 40em;
  min-height: 14em;
  padding: 2em;
  background: #383739;
  overflow: hidden;
  color: #fff;
}

