    padding: 3.125em;
  }
}

a.card:hover {
  box-shadow: none;
}

.card-header,
.card-body {
  position: relative;
  z-index: 1;
}

/**
 * HOVER
 */
a.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(56, 55, 57, 0.75);
  z-index: 1;
  transition: 200ms ease;
}

a.card::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1;
  width: 3.125em;
  height: 3.125em;
  background: #e35205 url("data:image/svg+xml,%3Csvg width='22' height='10' viewBox='0 0 22 10' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M.675 5.944l9.98-.004h8.03l-2.524 2.538a.66.66 0 0 0-.191.468.66.66 0 0 0 .191.467l.393.395a.65.65 0 0 0 .464.192.65.65 0 0 0 .464-.192l4.327-4.341A.653.653 0 0 0 22 5.001a.653.653 0 0 0-.191-.468L17.482.192A.65.65 0 0 0 17.018 0a.65.65 0 0 0-.464.192l-.393.394a.653.653 0 0 0 0 .922l2.553 2.552H.666A.681.681 0 0 0 0 4.736v.557c0 .363.313.651.675.651z' fill='%23fff'/%3E%3C/svg%3E") no-repeat center center;
  background-size: 50% auto;
  transition: 200ms ease;
  width: 2em;
  height: 2em;
}

@media (min-width: 800px) {
  a.card::after {
    width: 3.125em;
    height: 3.125em;
  }
}

/**
