  }
}

@media (min-width: 40rem) {
  .card-title {
    font-size: 1.75em;
  }
}

/**
 * BODY
 */
.card-body {
  padding-top: 1.25em;
  padding-bottom: 1.25em;
  border-top: 1px solid #fff;
  max-height: 20em;
  transition: opacity 200ms ease, padding 200ms ease;
  margin-bottom: -1.5em;
}

