  font-weight: 500;
  font-size: calc( 1rem + (1.5 - 1) * (100vw - 20rem) / (87.5 - 20));
  font-stretch: normal;
  font-style: normal;
  line-height: 1.1;
  margin-bottom: 0.5em;
}

.hero-title {
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  font-size: calc( 2rem + (3 - 2) * (100vw - 20rem) / (87.5 - 20));
  line-height: 1.13;
}

.hero-title::after {
  content: "\00a0";
  padding-left: 0.0625em;
  background: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle fill='%23e35205' cx='9' cy='9' r='9'%3E%3C/circle%3E%3C/svg%3E") no-repeat right 78%;
  background-size: 0.25em 0.25em;
}

.hero-action {
  margin-top: 1rem;
}

@media (min-width: 560px) {
  .hero-text {
    margin-left: 8.3%;
    margin-right: 8.3%;
  }
}

/**
 * TEXT WIDTH VARIANT
 */
@media (min-width: 800px) {
  .hero:not(.hero--text-full) .hero-text {
    flex-basis: 37%;
    min-width: 32rem;
  }
}

/**
 * SIZE VARIANT
 */
.hero--medium {
  min-height: calc( 14rem + (28 - 14) * (100vw - 20rem) / (87.5 - 20));
}

.hero--medium .hero-title-prefix {
  font-size: calc( 1rem + (1.5 - 1) * (100vw - 20rem) / (87.5 - 20));
}

.hero--medium .hero-title {
  font-size: calc( 2.5rem + (4 - 2.5) * (100vw - 20rem) / (87.5 - 20));
  line-height: 1.1;
}

.hero--medium .hero-action {
  margin-top: 2rem;
}

.hero--large {
  min-height: calc( 24rem + (40 - 24) * (100vw - 20rem) / (87.5 - 20));
}

.hero--large .hero-title-prefix {
  font-size: calc( 1.125rem + (2 - 1.125) * (100vw - 20rem) / (87.5 - 20));
}

.hero--large .hero-title {
  font-size: calc( 3rem + (4.75 - 3) * (100vw - 20rem) / (87.5 - 20));
  line-height: 0.89;
}

.hero--large .hero-action {
  margin-top: 3.25rem;
}

.hero--align-left .hero-text {
  margin-left: 0;
}

.hero-form {
  position: relative;
  max-width: 20rem;
  margin-left: auto;
}

.hero-form .main {
  background: #f7f7f7;
  padding: 3rem;
}

.hero-form h3 {
  text-transform: uppercase;
  color: #e35205;
}

.hero-form p {
  color: #383739;
}

.hero-form .button {
  width: 100%;
}

.hero-form .button:not(.button--simple):not(.button--small) {
  min-width: 6rem;
}

.infowindow {
  font-family: brand-system-fallback, sans-serif;
  line-height: 1.4;
  padding: 0.5em;
}

.infowindow p:not(:last-child) {
  margin-bottom: 0.5em;
}

.infowindow p:last-child {
  margin-bottom: 0;
}

.infowindow-actions {
  display: flex;
  align-items: center;
  border-top: 1px solid rgba(204, 204, 204, 0.5);
  padding-top: 0.75em;
  margin-top: 0.5em;
}

.infowindow-action {
  appearance: none;
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  font: inherit;
  cursor: pointer;
  color: inherit;
  background: transparent;
  box-shadow: none;
  display: flex;
  align-items: center;
}

.infowindow-action:hover {
  box-shadow: none;
}

.infowindow-action svg {
  width: 0.75rem;
  height: 0.75rem;
  opacity: 0.6;
  margin-right: 0.5em;
}

