  margin-left: 1rem;
}

.infowindow-action:hover, .infowindow-action:focus {
  color: #383739;
  text-decoration: underline;
}

.infowindow-action:hover svg, .infowindow-action:focus svg {
  opacity: 1;
}

/**
 * LIST
 */
.listings {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-left: -0.25em;
  margin-right: -0.25em;
}

@media (min-width: 560px) {
  .listings {
    margin-left: -0.5em;
    margin-right: -0.5em;
  }
}

@media (min-width: 800px) {
