    margin-right: -0.75em;
  }
}

@media (min-width: 0) {
  .listings {
    width: calc(100% + 0.5em);
  }
}

@media (min-width: 560px) {
  .listings {
    width: calc(100% + 1em);
  }
}

@media (min-width: 800px) {
  .listings {
    width: calc(100% + 1.5em);
  }
}

.listings .listings-item {
  min-width: 0;
  flex: 0 1 auto;
  width: 50%;
  flex-basis: 50%;
}

@media (min-width: 560px) {
  .listings .listings-item {
    width: 33.3333%;
    flex-basis: 33.3333%;
  }
}

@media (min-width: 1000px) {
  .listings .listings-item {
    width: 25%;
    flex-basis: 25%;
  }
}

@media (min-width: 1500px) {
  .listings .listings-item {
    width: 13em;
    flex-basis: 13em;
  }
}

@media (min-width: 1500px) {
  .listings .listings-item--large {
    width: 18em;
    flex-basis: 18em;
  }
}

.listings--loading {
  opacity: 0.5;
  cursor: wait;
}

.listings-item {
  padding: 0.25em;
}

@media (min-width: 560px) {
  .listings-item {
    padding: 0.5em;
  }
}

@media (min-width: 800px) {
  .listings-item {
    padding: 0.75em;
  }
}

/**
 * GENERAL
 */
.listing {
  display: inline-block;
  vertical-align: top;
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 13em;
  color: inherit;
}

a.listing:hover {
  box-shadow: none;
}

