 * LISTING IMAGE
 */
.listing-image {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f7f7f7;
  max-height: 15.625em;
  width: 100%;
}

.listing-image-img,
.listing-image-img svg {
