  a.listing .listing-overlay::before,
  a.listing .listing-image::after {
    width: 3.125em;
    height: 3.125em;
  }
}

/**
 * LISTING CONTENT
 */
.listing-content {
  padding: 0.5em;
  background: #fff;
}

.listing-title,
.listing-body {
  margin: 0;
}

.listing-title {
  font-size: 0.9375em;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
}

.listing-title:not(:last-child) {
  margin-bottom: 0.25em;
}

.listing-title a:hover {
  box-shadow: none;
}

.listing-body {
  font-size: 0.75em;
  color: #6d6e70;
}

@media (min-width: 560px) {
  .listing-body {
    font-size: 0.875em;
  }
}

.listing-body p:not(.listing-meta) {
  margin-bottom: 0.5em;
}

.listing-meta {
  margin-top: 0.5em;
  margin-bottom: 0;
  font-style: italic;
}

/**
 * LISTING FLAG
 */
.listing-flag {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  padding: 0.25em 1em;
  color: #fff;
  background: #383739;
  text-transform: uppercase;
  font-weight: bold;
  font-stretch: condensed;
  font-style: normal;
  font-size: 0.75rem;
  letter-spacing: 0.05em;
}

/**
 * HOVER
 */
a.listing:not(:hover) .listing-image::after,
a.listing:not(:hover) .listing-overlay::before {
  transform: translateY(100%);
}

/**
 * LARGE VARIANT
 */
.listing--large {
  max-width: 18em;
}

.listing--large .listing-image {
  max-height: 21.875em;
}

.listing--large .listing-title {
  font-size: calc( 1em + (1.375 - 1) * (100vw - 20rem) / (87.5 - 20));
  margin-bottom: 0.375em;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
  hyphens: auto;
  hyphens: inherit;
}

.listing--large .listing-body {
  font-size: 0.9375em;
}

.listing--large .listing-content {
  padding: 0.5em 0;
}

@media (min-width: 1000px) {
  .listing--large .listing-content {
    padding: 1em;
  }
}

/**
 * PEEK
 */
.listing-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: inherit;
  background: inherit;
}

a.listing .listing-overlay::before {
  z-index: 0;
  bottom: 100%;
}

a.listing .listing-overlay::after {
  content: "";
  background: inherit;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
}

.listing-overlay .listing-title,
.listing-overlay .listing-body {
  position: relative;
  z-index: 2;
}

.listing--peek .listing-body {
  overflow: hidden;
  text-overflow: ellipsis;
  transition: 350ms ease;
}

.listing--peek .listing-content {
  position: relative;
}

.listing--peek:not(:hover) .listing-image {
  flex-shrink: 0;
}

.listing--peek:not(:hover) .listing-body {
  max-height: 0;
  opacity: 0;
}

.listing--peek:not(:hover) .listing-overlay::before {
  transform: translateY(100%);
}

.listing--peek:hover .listing-body {
  max-height: 10em;
}

@media (max-width: 999px) {
  .listing-overlay {
    display: none;
  }
}

.loader {
  overflow: hidden;
  text-indent: -9999em;
  white-space: nowrap;
  display: inline-block;
