 */
.pagination {
  display: flex;
  align-items: center;
  flex-flow: row wrap;
}

/**
 * PAGINATOR
 */
.paginator {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

/**
 * PAGINATION LIST
 */
.pagination-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 1em;
}

/**
 * PAGINATION LINKS
 */
.pagination-link {
  appearance: none;
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  font: inherit;
  cursor: pointer;
  color: inherit;
  background: transparent;
  box-shadow: none;
  display: inline-block;
  padding: 0.375em 0.5em;
  font-weight: normal;
  font-size: 1.125rem;
  font-style: normal;
  font-stretch: normal;
  color: #383739;
  border-radius: 0.125em;
  min-width: 2.1875rem;
  text-align: center;
}

.pagination-link:hover {
  background-color: rgba(204, 204, 204, 0.5);
  box-shadow: none;
}

.pagination-link--previous,
.pagination-link--next {
  overflow: hidden;
  text-indent: -9999em;
  white-space: nowrap;
  width: 2em;
  background: no-repeat center center;
}

.pagination-link--previous.pagination-link--disabled,
.pagination-link--next.pagination-link--disabled {
  opacity: 0.3;
}

.pagination-link--previous {
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='13' viewBox='0 0 8 13' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.994 12.249L.75 7.149a.838.838 0 0 1-.25-.65.839.839 0 0 1 .25-.65L5.994.753a.898.898 0 0 1 1.247 0 .841.841 0 0 1 0 1.214L2.7 6.5l4.542 4.534a.842.842 0 0 1 0 1.215.9.9 0 0 1-1.247 0z' fill='%23383739' fill-rule='evenodd'/%3E%3C/svg%3E");
}

.pagination-link--next {
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='13' viewBox='0 0 8 13' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.006.751l5.243 5.1a.838.838 0 0 1 .25.65.839.839 0 0 1-.25.65l-5.243 5.097a.898.898 0 0 1-1.247 0 .841.841 0 0 1 0-1.214L5.3 6.5.759 1.966a.842.842 0 0 1 0-1.215.9.9 0 0 1 1.247 0z' fill='%23383739' fill-rule='evenodd'/%3E%3C/svg%3E");
}

.pagination-link--active {
  font-weight: bold;
}

.pagination-link--active, .pagination-link--active:hover {
  background-color: #e35205;
  color: #fff;
}

.pagination-link--disabled, .pagination-link--disabled:hover {
  background-color: transparent;
  color: #ccc;
  cursor: not-allowed;
}

/**
 * PAGINATION DOTS
 */
.pagination-break {
  padding: 0.375em 0.5em;
  color: #6d6e70;
}

/**
 * PER PAGE SELECTOR
 */
.pagination-per-page {
  margin-bottom: 1rem;
}

/**
 * LAYOUT
 */
.product-hero {
  position: relative;
  min-height: calc( 24rem + (40 - 24) * (100vw - 20rem) / (87.5 - 20));
}

.product-hero-header:not(:last-child),
.product-hero-body:not(:last-child),
.product-hero-footer:not(:last-child) {
  margin-bottom: calc( 1.5rem + (3 - 1.5) * (100vw - 20rem) / (87.5 - 20));
}

